@property --rotate {
  syntax: "<angle>";
  initial-value: 132deg;
  inherits: false;
}
@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

:root {
  --card-height: 65vh;
  --card-width: calc(var(--card-height) / 1.5);
}

body {
  min-height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 2rem;
  padding-bottom: 2rem;
  box-sizing: border-box;
}

.card {
  background: #1c1f21;
  width: 300px; /* Set a fixed width for all cards */
  height: 150px; /* Set a shorter height for all cards */
  padding: 3px;
  position: relative;
  border-radius: 6px;
  display: flex;
  flex-direction: row; /* Change to row to place image and text side by side */
  align-items: center;
  font-size: 1.5em;
  cursor: pointer;
  font-family: sans-serif;
  color: #D3D9D4;
  margin: 0 25px;
}
.card-top-right-image-container {
  position: absolute;
  top: 20px;
  right: 20px;
  display: flex;
  gap: 5px;
}

.card-small-image {
  width: 200px;
  height: 50px;
}

.card-image {
  width: 100%;
  height: auto;
}
.card-buttons {
  display: flex;
  justify-content: center;
  margin-top: 16px;
}

.card-button {
  margin: 0 30px;
  padding: 10px 46px;
  width: 200px;
  border: none;
  border-radius: 4px;
  background-color: #6b74c5;
  color: white;
  text-align: center; /* Ensure text is centered */
  text-decoration: none; /* Remove underline from links */
  display: inline-block; /* Ensure it behaves like a button */
  cursor: pointer;
  font-size: 14px; /* Smaller font size for button text */

}


.card-button:hover {
  background-color: #404676;
}


.card-text, .card-hover-text {
  text-align: center; /* Align text to the left */
  font-family: 'Raleway', sans-serif; /* Apply the imported font */

}

.card-image {
  width: 500px; /* Set a fixed width for the image */
  height: 500px; /* Set a fixed height for the image */
  margin-right: 10px; /* Add some space between the image and the text */
  border-radius: 10px; /* Make the image a rounded square */
}

.horizontal-card {
  flex: 1; /* Make the card stretch horizontally */
}

.card:hover {
  color: rgb(255, 255, 255);
  transition: color 1s;
}

.card:hover:before,
.card:hover:after {
  animation: none;
  opacity: 0;
}

.card::before {
  content: "";
  width: 104%;
  height: 102%;
  border-radius: 8px;
  background-image: linear-gradient(
    var(--rotate),
    #3b406d,
    #6b74c5 43%,
    #72628d
  );
  position: absolute;
  z-index: -1;
  top: -1%;
  left: -2%;
  animation: spin 2.5s linear infinite;
}

.card::after {
  position: absolute;
  content: "";
  top: calc(var(--card-height) / 6);
  left: 0;
  right: 0;
  z-index: -1;
  height: 100%;
  width: 100%;
  margin: 0 auto;
  transform: scale(0.8);
  filter: blur(calc(var(--card-height) / 6));
  background-image: linear-gradient(
    var(--rotate),
    #3b406d,
    #6b74c5 43%,
    #72628d
  );
  opacity: 1;
  transition: opacity 0.5s;
  animation: spin 2.5s linear infinite;
}

.devolotics-card {
  width: 80%;
  height: auto;
  margin-bottom: 20px;
}

@keyframes spin {
  0% {
    --rotate: 0deg;
  }
  100% {
    --rotate: 360deg;
  }
}

a {
  color: #212534;
  text-decoration: none;
  font-family: sans-serif;
  font-weight: bold;
  margin-top: 2rem;
}