body {
    background-color: #0d0e0e;
  }
  
  ::-webkit-scrollbar {
    width: 8px;
  }
  
  ::-webkit-scrollbar-track {
    background: #2F2F31;
  }
  
  ::-webkit-scrollbar-thumb {
    background: #262627;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: #2F2F31;
  }
  
  canvas {
    display: block;
  }
  
  .home {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 100vh;
  }
  
  .intro {
    position: absolute;
    top: 46.5%;
    left: 15%;
    transform: translate(-50%, -50%);
    color: #D3D9D4;
    font-size: 30px;
  }

  .down-arrow {
    position: absolute;
    top: calc(100vh - 80px);
    left: calc(50% - 14px);
    width: 0;
    height: 30px;
    border: 2px solid;
    border-radius: 2px;
    animation: jumpInfinite 1.5s infinite;
    color: #D3D9D4;
  }
  
  .down-arrow:after {
    content: " ";
    position: absolute;
    top: 12px;
    left: -10px;
    width: 16px;
    height: 16px;
    border-bottom: 4px solid;
    border-right: 4px solid;
    border-radius: 4px;
    transform: rotateZ(45deg);
  }
  
  @keyframes jumpInfinite {
    0% {
      margin-top: 0;
    }
    50% {
      margin-top: 20px;
    }
    100% {
      margin-top: 0;
    }
  }
  