.resume-button, .projects-button {
    text-decoration: none;
    color: inherit;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 29px;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.resume-button:hover, .projects-button:hover {
    background-color: #2c3052;
}
